import ChatEmoji from './ChatEmoji'
import util from '../../utils'
import config from '../../configs'
import axios from 'axios'

export default {
  components: {
    ChatEmoji
  },
  updated () {
    window.document.body.addEventListener('click', () => {
      this.isEmojiShown = false
    })
  },
  props: {
    type: String,
    scene: String,
    to: String,
    isRobot: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  watch: {
    continueRobotAccid (curVal, oldVal) {
      if (curVal && this.robotInfos[curVal]) {
        this.msgToSent = `@${this.robotInfos[curVal].nick} `
      }
      // 重置
      this.$store.dispatch('continueRobotMsg', '')
    },
    msgToSent (curVal, oldVal) {
      if (this.isRobot) {
        return
      }
      let indexAt = this.msgToSent.indexOf('@')
      if (indexAt >= 0 && (indexAt === this.msgToSent.length - 1)) {
        if (this.robotslist && this.robotslist.length > 0) {
          this.isRobotListShown = true
        }
      } else if (this.isRobotListShown === true) {
        this.isRobotListShown = false
      }
    },
    inputNum(nVal,oVal){
    	if(nVal>100){
      	this.inputNum=100;
    	}else if(nVal<1){
    		this.inputNum=1;
    	}
    },
    inputAmount(newVal,oldVal){
    	if(newVal>10000){
      	this.inputAmount=10000;
    	}else if(newVal<1){
    		this.inputAmount=1;
    	}
    }


  },
  data () {
    return {
      isEmojiShown: false,
      isRobotListShown: false,
      msgToSent: '',
      icon1: `${config.resourceUrl}/im/chat-editor-1.png`,
      icon2: `${config.resourceUrl}/im/chat-editor-2.png`,
      icon3: `${config.resourceUrl}/im/chat-editor-3.png`,
      popupShow: false,
      popupPLShow: false,
      tabsParam:['最新评论','打赏排行'],
      nowIndex: 0,
      showScrollBox: false,
      showScrollBox2: false,
      showHideOnBlur: false,
      isImgHide: false,
      maskShowHide: true,
      teacher_if:this.$store.state.haha.teacher_if,
      position: 'middle',
      redEnvelopeShow: false,
      inputNum:'',
      inputAmount:'',
      result: null
    }
  },
  computed: {
    continueRobotAccid () {
      return this.$store.state.continueRobotAccid
    },
    robotslist () {
      return this.$store.state.robotslist
    },
    robotInfos () {
      return this.$store.state.robotInfos
    },
    robotInfosByNick () {
      return this.$store.state.robotInfosByNick
    }
  },
  methods: {
    toggleTabs:function(index){
      this.nowIndex=index;
    },

    // inputFunc(){
    //   var x = document.getElementById("myInput");
    //   var z = document.getElementById("myIconShowPlass");
    //   var c = document.getElementById("myBtnShowPlass");
    //   if(x.value == "" || x.value == undefined || x.value == null){
    //     z.style = "";
    //     c.style.display = "none";
    //   }else{
    //     z.style.display = "none";
    //     c.style = "";
    //   }
    // },

    clickHB () {
      // this.showScrollBox = true;
      this.popupShow = true;
      // this.maskShowHide = true;
    },
    fadeMask () {
      this.popupShow = false;
    },
    clickStudentsPL () {
      this.popupPLShow = true;
    },
    clickTeacherPL () {
      this.showScrollBox = true;
    },
    clickTeacherML () {
      this.showScrollBox = true;
    },
    clickOpInput () {
      this.showHideOnBlur = true;
      document.getElementById("inputId").readonly = false;
      document.getElementById("inputId").blur();
      document.getElementById("textareaId").focus();
    },
    clickShutDown () {
      this.popupPLShow = false;
    },
    removeTextMsg () {
      this.showHideOnBlur = false;
    },
    sendHbMsg () {
			// this.redEnvelopeShow = true;
			// this.popupShow = false;
      this.showScrollBox = true
    },
    clickSendRedEnvelope () {
      this.redEnvelopeShow = false;
    },

    //发送消息
    sendTextMsg () {
      // var z = document.getElementById("myIconShowPlass");
      // var c = document.getElementById("myBtnShowPlass");
      // if(c.style.display != "none"){
      //   z.style = "";
      //   c.style.display = "none";
      // }else{
      //   z.style.display = "none";
      //   c.style = "";
      // }

//    var x =  document.getElementById("clickSendDown");
//    var y = document.getElementById("textareaId");
//    if(y.value == "" || y.value == undefined || y.value == null){
//      x.style = "";
//    }else{
//      this.showHideOnBlur = false;
//    }
      this.showHideOnBlur = false;
      if (/^\s*$/.test(this.msgToSent)) {
        this.$vux.alert.show({
          title: '请不要刷屏'
        })
        return
      } else if (this.msgToSent.length > 800) {
        this.$vux.alert.show({
          title: '请不要超过800个字'
        })
        return
      }
      this.msgToSent = this.msgToSent.trim()
        console.log('确认发送消息')
      if (this.type === 'session') {
        // 如果是机器人
        if (this.isRobot) {
          this.$store.dispatch('sendRobotMsg', {
            type: 'text',
            scene: this.scene,
            to: this.to,
            robotAccid: this.to,
            // 机器人后台消息
            content: this.msgToSent,
            // 显示的文本消息
            body: this.msgToSent
          })
        } else {
          let robotAccid = ''
          let robotText = ''
          let atUsers = this.msgToSent.match(/@[^\s@$]+/g)
          if (atUsers) {
            for (let i = 0; i < atUsers.length; i++) {
              let item = atUsers[i].replace('@', '')
              if (this.robotInfosByNick[item]) {
                robotAccid = this.robotInfosByNick[item].account
                robotText = (this.msgToSent + '').replace(atUsers[i], '').trim()
                break
              }
            }
          }
          if (robotAccid) {
            if (robotText) {
              this.$store.dispatch('sendRobotMsg', {
                type: 'text',
                scene: this.scene,
                to: this.to,
                robotAccid,
                // 机器人后台消息
                content: robotText,
                // 显示的文本消息
                body: this.msgToSent
              })
            } else {
              this.$store.dispatch('sendRobotMsg', {
                type: 'welcome',
                scene: this.scene,
                to: this.to,
                robotAccid,
                // 显示的文本消息
                body: this.msgToSent
              })
            }
          } else {
            this.$store.dispatch('sendMsg', {
              type: 'text',
              scene: this.scene,
              to: this.to,
              text: this.msgToSent
            })
          }
        }
      } else if (this.type === 'chatroom') {
        console.log('用户发送信息')
        let robotAccid = ''
        let robotText = ''
        let atUsers = this.msgToSent.match(/@[^\s@$]+/g)
        if (atUsers) {
          for (let i = 0; i < atUsers.length; i++) {
            let item = atUsers[i].replace('@', '')
            if (this.robotInfosByNick[item]) {
              robotAccid = this.robotInfosByNick[item].account
              robotText = (this.msgToSent + '').replace(atUsers[i], '').trim()
              break
            }
          }
        }
        if (robotAccid) {
          if (robotText) {
            this.$store.dispatch('sendChatroomRobotMsg', {
              type: 'text',
              robotAccid,
              // 机器人后台消息
              content: robotText,
              // 显示的文本消息
              body: this.msgToSent
            })
          } else {
            this.$store.dispatch('sendChatroomRobotMsg', {
              type: 'welcome',
              robotAccid,
              // 显示的文本消息
              body: this.msgToSent
            })
          }
        } else {

            console.log('执行store sendChatroomMsg commit  type-TEXT ')
          this.$store.dispatch('sendChatroomMsg', {
            type: 'text',
            text: this.msgToSent
          })
        }
      }
      this.msgToSent = ''
    },
    sendPlayMsg () {
      // 发送猜拳消息
      if (this.type === 'session') {
        this.$store.dispatch('sendMsg', {
          type: 'custom',
          scene: this.scene,
          to: this.to,
          pushContent: '[猜拳]',
          content: {
            type: 1,
            data: {
              value: Math.ceil(Math.random()*3)
            }
          }
        })
      } else if (this.type === 'chatroom') {
        this.$store.dispatch('sendChatroomMsg', {
          type: 'custom',
          pushContent: '[猜拳]',
          content: {
            type: 1,
            data: {
              value: Math.ceil(Math.random()*3)
            }
          }
        })
      }
    },
    sendFileMsg () {
      let ipt = this.$refs.fileToSent
      if (ipt.value) {
        if (this.type === 'session') {
          this.$store.dispatch('sendFileMsg', {
            scene: this.scene,
            to: this.to,
            fileInput: ipt
          })
        } else if (this.type === 'chatroom') {
          this.$store.dispatch('sendChatroomFileMsg', {
            fileInput: ipt
          })
        }
      }
    },

    // 微信语音(按下)
    startRecord (e) {
      e.preventDefault();
      START = new Date().getTime();
      this.isImgHide = true;
      recordTimer = setTimeout(function(){
	  		wx.startRecord({
	  			success: function(){
	  				localStorage.rainAllowRecord = 'true';
	  			},
	  			cancel: function(){
	  				alert('用户拒绝授权录音');
						window.location.reload();
	  			}
	  		})
      },200);
    },
    // 微信语音(松开)
    stopRecord (e) {
      e.preventDefault();
      END = new Date().getTime();
      this.isImgHide = false;
      useTime = END - START;
      if ((END - START) < 200) {
        END = 0;
        START = 0;
        // 小于200ms则不录音
        clearTimeout(recordTimer);
        alert("录音时间不能小于200ms");
      } else {
        wx.stopRecord({
          success: function (res) {
            localId = res.localId;
            // 上传
            wx.uploadVoice({
              localId: localId,
              isShowProgressTips: 1, // 显示进度
              success: function (res) {
                serverId = res.serverId;
                // 提交到后台
                axios({
                  url:"/m/index/yunxin/save",
                  method:"post",
                  data:{
                    uid      : window.document.getElementById('uid').value,
                    serverId : serverId,
                    length   : useTime
                  }
                }).then(function(res){
                  // alert(JSON.stringify(res))
                }).catch(function(err){
                  alert('上传失败！'+JSON.stringify(err))
                })
              }
            });
          },
          fail: function (res) {
            alert('录制失败！'+JSON.stringify(res));
          }
        });
      }
    },

    showEmoji () {
      this.isEmojiShown = true
    },
    hideEmoji () {
      this.isEmojiShown = false
    },
    addEmoji (emojiName) {
      this.msgToSent += emojiName
      this.hideEmoji()
    },
    chooseRobot (robot) {
      if (robot && robot.account) {
        let len = this.msgToSent.length
        if (len === 0 || this.msgToSent[len-1] !== '@') {
          this.msgToSent += '@' + robot.nick + ' '
        } {
          this.msgToSent += robot.nick + ' '
        }
      }
    },
    hideRobotList () {
      this.isRobotListShown = false
    }
  }
}



